@use '../../../styles/Variables.scss' as *;

/* START: Misc */
:root {
    --box-shadow-width-thumbnail: 0vw 0vw 10vw;
    --box-shadow-opacity-thumbnail: 0.2;
}

.featured-work h2 {
    text-align: center;
}

.featured-work .highlight {
    color: var(--hovered-color-links);
}

.featured-project h3 {
    margin-bottom: 0;
}

.featured-project h4 {
    margin: 0;
}
/* END: Misc */

/* START: FEATURED PROJECT */
.featured-project {
    display: grid;
    margin-bottom: 2vw;
    outline: var(--outline-width) var(--outline-style) var(--outline-color);
    gap: 1vw;

    &.odd {
        grid-template-columns: 2fr 3fr;

        .img-button-container{
            order: 2;
        }

        .featured-work-text{
            order: 1;
        }
    }

    &.even {
        grid-template-columns: 3fr 2fr;

        .img-button-container{
            order: 1;
        }

        .featured-work-text{
            order: 2;
        }
    }
}

.featured-project .img-button-container {
    position: relative;
    align-content: center;
    width: 100%;
    height: fit-content;
}

/* START: thumbnail */
.featured-project .thumbnail {
    width: 100%;
    height: auto;
    border-radius: var(--border-radius-large);
    transition: filter var(--transition-time) ease;
    align-content: center;
}

.featured-project:hover .thumbnail {
    filter: brightness(80%);
}
/* END: thumbnail */

/* START: project-tags */
.featured-project .img-button-container .project-tags {
    position: absolute;
    margin: 1vw;
    flex-direction: column;
}

.featured-project.odd .img-button-container .project-tags {
    top: 0%;
    left: 0%;
}

.featured-project.even .img-button-container .project-tags {
    top: 0%;
    right: 0%;
    align-items: flex-end;
}
/* END: project-tags */

/* START: date */
.featured-project .img-button-container .date {
    position: absolute;
    background-color: var(--outline-color);
    margin: 1vw;
    padding: var(--padding-small);
    border-radius: var(--border-radius-small);
}

.featured-project.odd .img-button-container .date {
    bottom: 0%;
    left: 0%;
}

.featured-project.even .img-button-container .date {
    bottom: 0%;
    right: 0%;
}
/* END: date */

/* START: explore-project-button */
.featured-project .img-button-container .explore-project-button {
    background-color: var(--button-fill-color);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    border-radius: var(--border-radius-small);
    padding: var(--padding-small);
    transition: opacity var(--transition-time) ease, transform var(--transition-time) ease;
    width: fit-content;
    text-align: center;
    white-space: nowrap;
    z-index: 1;
}

.featured-project:hover .img-button-container .explore-project-button {
    opacity: 1;
}

.featured-project .explore-project-button:hover {
    cursor: pointer;
    transform: translate(-50%, -50%) scale(var(--transform-scale)); /* Matrix Operations Order First Scale On Origin Then Translate */
}
/* END:  explore-project-button */

/* END: featured-project */

/* START: Mobile Support */
@media (max-width: $mobile-phone-width) {
    .featured-project.odd, .featured-project.even {
        grid-template-columns: 1fr;
        .img-button-container{
            order: unset;
        }
        .featured-work-text{
            order: unset;
        }
    }
}